
.page-title {
  padding: 0.8rem 0;
  margin-bottom: 1rem;
  background-color: LightGray;
}
.page-title img {
  width: 0.5rem;
  height: 0.875rem;
  position: absolute;
  margin: 0 1rem;
}
.page-title p {
  font-size: 1rem;
  text-align: center;
}
.on-parking-status-timer {
  width: 21rem;
  height: 12rem;
  text-align: center;
  margin: 2rem auto;
  background-color: #fff;
  border-radius: 0.5rem;
}
.on-parking-status-timer p:nth-child(1) {
  font-size: 1.3rem;
  line-height: 3rem;
}
.on-parking-status-timer p:nth-child(2) {
  font-size: 3.5rem;
  line-height: 3.5rem;
}
.on-parking-status-timer p:nth-child(3) {
  font-size: 1rem;
  line-height: 3rem;
}
.on-parking-status-button {
  width: 21rem;
  height: 3rem;
  text-align: center;
  line-height: 3rem;
  font-size: 1rem;
  background-color: #FF3030;
  color: #fff;
  margin: 2rem auto;
  border-radius: 0.5rem;
}



