
.page-title {
  padding: 0.8rem 0;
  margin-bottom: 1rem;
  background-color: LightGray;
}
.page-title img {
  width: 0.5rem;
  height: 0.875rem;
  position: absolute;
  margin: 0 1rem;
}
.page-title p {
  font-size: 1rem;
  text-align: center;
}
table, tr, th, td {
  font-size: 1rem;
  margin: 1rem auto;
  width: 20rem;
  height: 3rem;
  border: 1px solid #000;
  text-align: center;
}
#serial-number {
  color: blue;
  font-weight: bold;
}
#parking-status {
  color: red;
  font-weight: bold;
}
.ready-parking-info {
  text-align: center;
  margin: 2rem 1rem;
}
.ready-parking-info p:nth-child(1) {
  font-size: 1rem;
  margin: 1rem auto;
}
.ready-parking-info p:nth-child(2) {
  font-size: 1.5rem;
  margin: 1rem auto;
  color: green;
}
.wait-user-operation {
  text-align: center;
  margin: 2rem 1rem;
}
.wait-user-operation p:nth-child(1) {
  font-size: 1rem;
  margin: 1rem auto;
}
.wait-user-operation p:nth-child(2) {
  font-size: 3rem;
  margin: 1rem auto;
}
.ready-parking-buttons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.ready-parking-buttons div {
  width: 10rem;
  height: 3rem;
  margin: 0rem 1rem;
  text-align: center;
  line-height: 3rem;
  border-radius: 0.5rem;
}
.ready-parking-buttons div:nth-child(1) {
  background-color: limegreen;
  color: #fff;
}
.ready-parking-buttons div:nth-child(2) {
  background-color: #FF3030;
  color: #fff;
}



