
.page-title {
  padding: 0.8rem 0;
  margin-bottom: 1rem;
  background-color: LightGray;
}
.page-title img {
  width: 0.5rem;
  height: 0.875rem;
  position: absolute;
  margin: 0 1rem;
}
.page-title p {
  font-size: 1rem;
  text-align: center;
}
.profile-info-items {
  padding: 0 0.8rem;
}
.profile-info-items img {
  width: 3rem;
  height: 3rem;
}
.profile-info-items div {
  margin: 1rem 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.profile-info-items div p:first-child {
  font-size: 1rem;
}
.profile-info-items div p:last-child {
  font-size: 0.8rem;
}



